@use 'base/variables' as v;
@use 'base/mixins' as m;
@use 'sass:color';

.acciones {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

    &--centrar {
        @extend .acciones;
        justify-content: center;
    }

    &__boton {
        color: v.$negro;
        font-size: 1.4rem;
        font-weight: v.$delgada;
    }
}

